"use client";

import * as React from "react";

import { cn } from "@/lib/utils";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogClose,
} from "@/components/ui/dialog";
import { STLArrowIcon, XIcon } from "@/icons";

export interface ModalProps {
  /** 모달 크기 */
  size?: "s" | "m" | "l" | "xl";
  /** 모달 열림 상태 */
  open?: boolean;
  /** 모달 열림/닫힘 상태 변경 핸들러 */
  onOpenChange?: (open: boolean) => void;
  /** 모달 제목 */
  title?: React.ReactNode;
  /** 모달 설명 */
  description?: React.ReactNode;
  /** 모달 본문 내용 */
  children?: React.ReactNode;
  /** 모달 하단 버튼 영역 */
  footer?: React.ReactNode;
  /** 추가 className */
  className?: string;
  /** 로딩 상태 (스플래시 표시) */
  loading?: boolean;
  /** 닫기 버튼 표시 (true: X 버튼, false: STL 아이콘) */
  showCloseButton?: boolean;
}

const Modal = ({
  size = "l",
  open,
  onOpenChange,
  title,
  description,
  children,
  footer,
  className,
  loading = false,
  showCloseButton = false,
}: ModalProps) => {
  // Size configuration
  const sizeConfig = {
    s: "max-w-[400px]",
    m: "max-w-[600px]",
    l: "max-w-[900px]",
    xl: "max-w-[1200px]",
  };

  // footer 자식 개수에 따른 정렬 (Fragment 내부 자식도 카운트)
  const getChildCount = (children: React.ReactNode): number => {
    if (!children) return 0;
    if (React.isValidElement(children) && children.type === React.Fragment) {
      return React.Children.count(children.props.children);
    }
    return React.Children.count(children);
  };
  const footerChildCount = getChildCount(footer);

  // 헤더 우측 아이콘 렌더링
  const renderHeaderIcon = () => {
    if (showCloseButton) {
      return (
        <DialogClose className="absolute right-4 top-4 rounded-sm opacity-70 transition-opacity hover:opacity-100 focus:outline-none disabled:pointer-events-none cursor-pointer">
          <XIcon size={24} />
          <span className="sr-only">닫기</span>
        </DialogClose>
      );
    }
    return (
      <div className="absolute right-4 top-4">
        <STLArrowIcon size={29} className="text-slate-200 dark:text-slate-600" />
      </div>
    );
  };

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className={cn(sizeConfig[size], className)} loading={loading}>
        {!loading && (
          <>
            {renderHeaderIcon()}
            {(title || description) && (
              <DialogHeader className="pr-10">
                {title && <DialogTitle>{title}</DialogTitle>}
                {description && <DialogDescription>{description}</DialogDescription>}
              </DialogHeader>
            )}

            {children && <div className="py-4">{children}</div>}

            {footer && (
              <div
                className={cn(
                  "flex flex-col-reverse gap-2 sm:flex-row w-full",
                  footerChildCount === 1 ? "sm:justify-end" : "sm:justify-between"
                )}
              >
                {footer}
              </div>
            )}
          </>
        )}
      </DialogContent>
    </Dialog>
  );
};

export { Modal };
